<template>
  <div>
    <import-preset-dialog
        v-if="show_dialog"
        :callback="dialog_callback"
        :initial-preset="dialog_preset"
    />
    <v-card>
      <v-card-title>
        <v-icon>mdi-microsoft-excel</v-icon>
        EXCEL取り込み
      </v-card-title>
      <v-file-input
          label="EXCELファイルを選択"
          v-model="file"
      ></v-file-input>
      <div v-if="file" class="ml-5 py-3">
        <v-btn @click="importFile">
          <v-icon>mdi-check</v-icon>
          適用
        </v-btn>
      </div>
    </v-card>
    <v-card>
      <v-card-title>
        <v-icon>mdi-cog</v-icon>
        取り込み行設定
      </v-card-title>
      <import-setting-panel-header />
      <import-setting-panel
          v-for="preset in import_presets"
          :key="preset.id"
          :preset="preset"
          :checked-term1="getChecked(1, preset.id)"
          :checked-term2="getChecked(2, preset.id)"
          @check-1="preset_id_term1 = preset.id"
          @check-2="preset_id_term2 = preset.id"
          @edit="openEditDialog(preset)"
          @delete="deletePreset(preset)"
      ></import-setting-panel>
      <div class="text-right ma-4">
        <v-btn @click="openCreateDialog">
          <v-icon>mdi-plus</v-icon>
          取り込み設定の追加
        </v-btn>
      </div>
    </v-card>
  </div>
</template>

<script>
import ImportSettingPanel from "@/components/ImportSettingPanel";
import ImportSettingPanelHeader from "@/components/ImportSettingPanelHeader";
import ImportPresetDialog from "@/components/ImportPresetDialog";
export default {
  components: {
    ImportSettingPanel,
    ImportSettingPanelHeader,
    ImportPresetDialog
  },
  data() {
    return {
      show_dialog: false,
      dialog_preset: null,
      dialog_callback: null,
      file: null
    }
  },
  computed: {
    import_settings() {
      return this.$store.state.import_settings
    },
    import_presets() {
      return this.$store.state.import_presets
    },
    preset_id_term1: {
      get() {
        return this.getPresetId(1)
      },
      set(preset_id) {
        this.$store.commit('set_preset_id', {
          term: 1,
          preset_id
        })
        console.log(this.import_settings)
      }
    },
    preset_id_term2: {
      get() {
        return this.getPresetId(2)
      },
      set(preset_id) {
        this.$store.commit('set_preset_id', {
          term: 2,
          preset_id
        })
      }
    },
    getPresetId() {
      return (term) => {
        const import_setting = this.import_settings.find(setting => setting.term === term)
        if (import_setting) {
          return import_setting.preset_id
        }
        console.error(`SETTING PRESET NOT FOUND. term: ${term}`)
      }
    },
    getChecked() {
      return (term, preset_id) => {
        return this.getPresetId(term) === preset_id
      }
    }
  },
  methods: {
    openCreateDialog() {
      this.dialog_preset = null
      this.show_dialog = true
      this.dialog_callback = (new_preset, e) => {
        if (!e) {
          this.$store.commit('add_import_preset', {preset: new_preset})
        }
        this.show_dialog = false
      }
    },
    openEditDialog(preset) {
      this.dialog_preset = preset
      this.show_dialog = true
      this.dialog_callback = (new_preset, e) => {
        if (!e) {
          this.$store.commit('set_import_preset', {preset: new_preset})
        }
        this.show_dialog = false
      }
    },
    deletePreset(preset) {
      this.$store.commit('delete_import_preset', {preset})
    },
    importFile() {
      alert('ファイルを取り込みました。')
    }
  }
}
</script>

