import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    lines: [
      {
        id: 0,
        name: "BD21",
        ready_times: [
          {pallet_num: 1, seconds: 25},
          {pallet_num: 2, seconds: 45},
          {pallet_num: 3, seconds: 65},
          {pallet_num: 4, seconds: 87},
        ],
        pickup_times: [
          {pallet_num: 1, seconds: 61},
          {pallet_num: 2, seconds: 120},
          {pallet_num: 3, seconds: 191},
          {pallet_num: 4, seconds: 261},
        ],
        finishing_time: 50,
        color: {R: 250, G: 250, B: 140, A: 0}
      },
      {
        id: 0,
        name: "BD22",
        ready_times: [
          {pallet_num: 1, seconds: 26},
          {pallet_num: 2, seconds: 46},
          {pallet_num: 3, seconds: 66},
          {pallet_num: 4, seconds: 86},
        ],
        pickup_times: [
          {pallet_num: 1, seconds: 60},
          {pallet_num: 2, seconds: 125},
          {pallet_num: 3, seconds: 190},
          {pallet_num: 4, seconds: 260},
        ],
        finishing_time: 50,
        color: {R: 50, G: 250, B: 240}
      },
    ],
    time_settings: [
      {
        term: 1,
        start: "08:00:00",
        breaktimes: [
          {id: 0, from: "08:30:00", to: "08:40:00"},
          {id: 1, from: "10:00:00", to: "10:20:00"},
        ]
      },
      {
        term: 2,
        start: "16:00:00",
        breaktimes: [
          {id: 0, from: "08:30:00", to: "08:40:00"},
          {id: 1, from: "10:00:00", to: "10:20:00"},
        ]
      },
    ],
    walking_times: [
      {start_id: 0, goal_id: 1, seconds: 30},
      {start_id: 1, goal_id: 0, seconds: 40}
    ],
    import_settings: [
      {
        term: 1,
        preset_id: 0
      },
      {
        term: 2,
        preset_id: 1
      }
    ],
    import_presets: [
      {
        id: 0,
        name: "B行",
        start_pitch: 1,
        excluding_pitches: [],
      },
      {
        id: 1,
        name: "I行",
        start_pitch: 2,
        excluding_pitches: [],
      },
      {
        id: 2,
        name: "P行",
        start_pitch: 3,
        excluding_pitches: [],
      }
    ]
  },
  mutations: {
    set_preset_id(state, {term, preset_id}) {
      const index = state.import_settings.findIndex(s => s.term === term)
      if (index >= 0) {
        state.import_settings.splice(index, 1, {
          ...state.import_settings[index],
          preset_id
        })
      }
    },
    set_line(state, {line}) {
      const index = state.lines.findIndex(l => l.id === line.id)
      if (index >= 0) {
        state.lines.splice(index, 1, line)
      } else {
        state.lines.push(line)
      }
    },
    add_line(state, {line}) {
      const id = state.lines.length > 0
                ? state.lines.reduce((a, b) => {
                    return Math.max(a, b.id)
                  }, -1) + 1
                : 0
      state.lines.push({
        ...line,
        id
      })
    },
    delete_line(state, {line}) {
      const index = state.lines.findIndex(item => item.id === line.id)
      state.lines.splice(index, 1)
    },
    set_import_preset(state, {preset}) {
      const index = state.import_presets.findIndex(l => l.id === preset.id)
      if (index >= 0) {
        state.import_presets.splice(index, 1, preset)
      } else {
        state.import_presets.push(preset)
      }
    },
    add_import_preset(state, {preset}) {
      const id = state.import_presets.length > 0
                ? state.import_presets.reduce((a, b) => {
                    return Math.max(a, b.id)
                  }, -1) + 1
                : 0
      state.import_presets.push({
        ...preset,
        id
      })
    },
    delete_import_preset(state, {preset}) {
      const index = state.import_presets.findIndex(item => item.id === preset.id)
      state.import_presets.splice(index, 1)
    },
    add_breaktime(state, {term, breaktime}) {
      const term_index = state.time_settings.findIndex(s => s.term === term)
      const id = state.time_settings[term_index].breaktimes.reduce((a, b) => {
        return Math.max(a.id, b.id)
      }).id + 1
      state.time_settings[term_index].breaktimes.push({
        ...breaktime,
        id
      })
    },
    update_breaktime(state, {term, breaktime}) {
      const term_index = state.time_settings.findIndex(s => s.term === term)
      const index = state.time_settings[term_index].breaktimes.findIndex(s => s.id === breaktime.id)
      state.time_settings[term_index].breaktimes.splice(index, 1, breaktime)
    },
    delete_breaktime(state, {term, breaktime}) {
      const term_index = state.time_settings.findIndex(s => s.term === term)
      const index = state.time_settings[term_index].breaktimes.findIndex(s => s.id === breaktime.id)
      state.time_settings[term_index].breaktimes.splice(index, 1)
    }
  },
  actions: {},
  modules: {}
})
