<template>
  <div>
    <line-edit-dialog v-if="show_dialog" :initial-line="dialog_line" :callback="dialog_callback"></line-edit-dialog>
    <v-card>
      <v-card-title>
        <v-icon>mdi-factory</v-icon>
        ライン設定
      </v-card-title>
      <line-table-panel-header />
      <line-table-panel
        v-for="(line, index) in lines"
        :key="index"
        :index="index"
        :line="line"
        @edit="openEditDialog(line)"
        @delete="deleteLine"
      />
      <div class="mt-3 text-right">
        <v-btn @click="openCreateDialog">
          <v-icon>mdi-plus</v-icon>
          ラインを追加
        </v-btn>
      </div>
    </v-card>
  </div>
</template>

<script>
import LineTablePanel from '@/components/LineTablePanel'
import LineTablePanelHeader from '@/components/LineTablePanelHeader'
import LineEditDialog from '@/components/LineEditDialog'

export default {
  components: {
    LineTablePanel,
    LineTablePanelHeader,
    LineEditDialog
  },
  data: () => ({
    dialog_callback: null,
    dialog_line: null,
    show_dialog: false
  }),
  computed: {
    lines() {
      return this.$store.state.lines
    },
  },
  methods: {
    openCreateDialog() {
      this.dialog_line = null
      this.show_dialog = true
      this.dialog_callback = (new_line, e) => {
        if (!e) {
          this.$store.commit('add_line', {line: new_line})
        }
        this.show_dialog = false
      }
    },
    openEditDialog(line) {
      this.dialog_line = line
      this.show_dialog = true
      this.dialog_callback = (new_line, e) => {
        if (!e) {
          this.$store.commit('set_line', {line: new_line})
        }
        this.show_dialog = false
      }
    },
    deleteLine(line) {
      this.$store.commit('delete_line', {line})
    }
  }
}
</script>

