<template>
  <div>
    <v-card>
      <v-card-title>
        <v-icon>mdi-clock</v-icon>
        時間設定
      </v-card-title>
      <v-container>
        <v-row>
          <v-col>
            <v-card>
              <v-card-title>１直</v-card-title>
              <v-container>
                <div>開始時刻</div>
                <v-row class="mb-4">
                  <v-col cols="3">
                    
                  </v-col>
                  <v-col cols="3">
                    <v-text-field
                        :value="time_settings_term1.start"
                        type="time"
                        hide-details
                        dense
                    ></v-text-field>
                  </v-col>
                </v-row>
                <div>停止時間</div>
                <v-row
                    v-for="(b, index) in time_settings_term1.breaktimes"
                    :key="index"
                >
                  <v-col>
                    <v-subheader>{{ index }}</v-subheader>
                  </v-col>
                  <v-col>
                    <v-text-field
                        label="停止時刻"
                        :value="b.from"
                        type="time"
                        hide-details
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-text-field
                        label="再開時刻"
                        :value="b.to"
                        type="time"
                        hide-details
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-icon v-on="on" v-bind="attrs" class="mt-5" @click="deleteBreaktime(1, b)">mdi-trash-can</v-icon>
                      </template>
                      停止時間の削除
                    </v-tooltip>
                  </v-col>
                </v-row>
              </v-container>
              <div class="text-right">
                <v-btn @click="addBreaktime(1)">
                  <v-icon>mdi-plus</v-icon>
                  停止時間の追加
                </v-btn>
              </div>
            </v-card>
          </v-col>
          <v-col>
            <v-card>
              <v-card-title>２直</v-card-title>
              <v-container>
                <div>開始時刻</div>
                <v-row class="mb-4">
                  <v-col cols="3">
                  </v-col>
                  <v-col cols="3">
                    <v-text-field
                        value="12:30:00"
                        type="time"
                        hide-details
                        dense
                    ></v-text-field>
                  </v-col>
                </v-row>
                <div>停止時間</div>
                <v-row
                    v-for="(b, index) in time_settings_term2.breaktimes"
                    :key="index"
                >
                  <v-col>
                    <v-subheader>{{ index }}</v-subheader>
                  </v-col>
                  <v-col>
                    <v-text-field
                        label="停止時刻"
                        :value="b.from"
                        type="time"
                        hide-details
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-text-field
                        label="再開時刻"
                        :value="b.to"
                        type="time"
                        hide-details
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-icon v-on="on" v-bind="attrs" class="mt-5" @click="deleteBreaktime(2, b)">mdi-trash-can</v-icon>
                      </template>
                      停止時間の削除
                    </v-tooltip>
                  </v-col>
                </v-row>
              </v-container>
              <div class="text-right">
                <v-btn @click="addBreaktime(2)">
                  <v-icon>mdi-plus</v-icon>
                  停止時間の追加
                </v-btn>
              </div>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
  </div>
</template>

<script>
export default {
  data: () => {
    return {

    }
  },
  computed: {
    time_settings() {
      return this.$store.state.time_settings
    },
    time_settings_term1() {
      return this.time_settings.find(s => s.term === 1)
    },
    time_settings_term2() {
      return this.time_settings.find(s => s.term === 2)
    }
  },
  methods: {
    deleteBreaktime(term, breaktime) {
      this.$store.commit('delete_breaktime', {
        term: term,
        breaktime: breaktime
      })
    },
    addBreaktime(term) {
      this.$store.commit('add_breaktime', {
        term: term,
        breaktime: {
          from: "00:00:00", to: "00:00:00"
        }
      })
    }
  }
}
</script>

