<template>
  <v-card class="mt-2" :style="panel_style">
    <v-container fluid>
      <v-row>
        <v-col>{{ index }}</v-col>
        <v-divider vertical style="margin: 0 -0.5px"  />
        <v-col>{{ line.name }}</v-col>
        <v-divider vertical style="margin: 0 -0.5px"  />
        <v-col>{{ getReadyTimes(1) }}</v-col>
        <v-divider vertical style="margin: 0 -0.5px"  />
        <v-col>{{ getReadyTimes(2) }}</v-col>
        <v-divider vertical style="margin: 0 -0.5px"  />
        <v-col>{{ getReadyTimes(3) }}</v-col>
        <v-divider vertical style="margin: 0 -0.5px"  />
        <v-col>{{ getReadyTimes(4) }}</v-col>
        <v-divider vertical style="margin: 0 -0.5px"  />
        <v-col>{{ getPickupTimes(1) }}</v-col>
        <v-divider vertical style="margin: 0 -0.5px"  />
        <v-col>{{ getPickupTimes(2) }}</v-col>
        <v-divider vertical style="margin: 0 -0.5px"  />
        <v-col>{{ getPickupTimes(3) }}</v-col>
        <v-divider vertical style="margin: 0 -0.5px"  />
        <v-col>{{ getPickupTimes(4) }}</v-col>
        <v-divider vertical style="margin: 0 -0.5px"  />
        <v-col>{{ finishing_time }}</v-col>
        <v-divider vertical style="margin: 0 -0.5px"  />
        <v-col>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-icon v-on="on" v-bind="attrs" @click="$emit('edit')">mdi-pen</v-icon>
            </template>
            ラインの編集
          </v-tooltip>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-icon v-on="on" v-bind="attrs" class="ml-2" @click="$emit('delete')">mdi-trash-can</v-icon>
            </template>
            ラインの削除
          </v-tooltip>
        </v-col>
      </v-row>
    </v-container>
  </v-card>
</template>

<script>
export default {
  props: {
    index: {
      type: Number,
      required: true
    },
    line: {
      type: Object,
      required: true
    }
  },
  computed: {
    getReadyTimes() {
      return (pallet_num) => {
        const ready_time = this.line.ready_times.find((ready_time) => ready_time.pallet_num === pallet_num)
        if (ready_time) {
          return ready_time.seconds
        }
        console.error(`READY TIME NOT FOUND. pallet_num: ${pallet_num}, this.lines: `, this.line)
      }
    },
    getPickupTimes() {
      return (pallet_num) => {
        const ready_time = this.line.pickup_times.find((pickup_time) => pickup_time.pallet_num === pallet_num)
        if (ready_time) {
          return ready_time.seconds
        }
        console.error(`PICKUP TIME NOT FOUND. pallet_num: ${pallet_num}, this.lines: `, this.line)
      }
    },
    finishing_time() {
      return this.line.finishing_time
    },
    panel_style() {
      const {R, G, B} = this.line.color
      return `background: rgb(${R}, ${G}, ${B});`
    }
  }
}
</script>
