<template>
  <v-card
    class="my-1 pa-3"
    width="100%"
  >
    <v-container fluid>
      <v-row>
        <v-col cols="1">
          <v-icon
              v-if="checkedTerm1"
              color="green"
              @click="handleClick(1)"
          >
            mdi-checkbox-marked-circle-outline
          </v-icon>
          <v-icon
              v-else
              @click="handleClick(1)"
          >
            mdi-checkbox-blank-circle-outline
          </v-icon>
        </v-col>
        <v-col cols="1">
          <v-icon
              v-if="checkedTerm2"
              color="green"
              @click="handleClick(2)"
          >
            mdi-checkbox-marked-circle-outline
          </v-icon>
          <v-icon
              v-else
              @click="handleClick(2)"
          >
            mdi-checkbox-blank-circle-outline
          </v-icon>
        </v-col>
        <v-col cols="2">
          {{ preset.name }}
        </v-col>
        <v-col cols="2">
          {{ preset.start_pitch }}
        </v-col>
        <v-col cols="2">
          {{ finish_pitch }}
        </v-col>
        <v-col cols="3">
          {{ excluding_pitches }}
        </v-col>
        <v-col cols="1">
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-icon v-on="on" v-bind="attrs" @click="$emit('edit')">mdi-pen</v-icon>
            </template>
            設定の編集
          </v-tooltip>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-icon v-on="on" v-bind="attrs" class="ml-2" @click="$emit('delete')">mdi-trash-can</v-icon>
            </template>
            設定の削除
          </v-tooltip>
        </v-col>
      </v-row>
    </v-container>
  </v-card>
</template>

<script>
export default {
  props: {
    preset: {
      type: Object,
      required: true
    },
    checkedTerm1: {
      type: Boolean,
      required: true
    },
    checkedTerm2: {
      type: Boolean,
      required: true
    },
  },
  computed: {
    finish_pitch() {
      return this.preset.finish_pitch
              ? this.preset.finish_pitch
              : '最後まで'
    },
    excluding_pitches() {
      return this.preset.excluding_pitches && this.preset.excluding_pitches.length > 0
            ? this.preset.excluding_pitches.join(',')
            : '-'
    }
  },
  methods: {
    handleClick(term) {
      if (!this.checked) {
        this.$emit('check-' + term)
      }
    }
  }
}
</script>

