<template>
  <v-dialog value="true" persistent>
    <v-card>
      <v-card-title>
        ラインの編集
      </v-card-title>
      <v-card-text>
        <div>
          <v-text-field
            label="ライン名"
            v-model="line.name"
          />
        </div>
        <div>
          準備時間 [sec/ピッチ]
        </div>
        <v-container>
          <v-row>
            <v-col>
              <v-text-field
                label="１パレット"
                type="number"
                v-model="line.ready_times[0].seconds"
              />
            </v-col>
            <v-col>
              <v-text-field
                label="２パレット"
                type="number"
                v-model="line.ready_times[1].seconds"
              />
            </v-col>
            <v-col>
              <v-text-field
                label="３パレット"
                type="number"
                v-model="line.ready_times[2].seconds"
              />
            </v-col>
            <v-col>
              <v-text-field
                label="４パレット"
                type="number"
                v-model="line.ready_times[3].seconds"
              />
            </v-col>
          </v-row>
        </v-container>
        <div>
          集荷時間 [sec/ピッチ]
        </div>
        <v-container>
          <v-row>
            <v-col>
              <v-text-field
                label="１パレット"
                type="number"
                v-model="line.pickup_times[0].seconds"
              />
            </v-col>
            <v-col>
              <v-text-field
                label="２パレット"
                type="number"
                v-model="line.pickup_times[1].seconds"
              />
            </v-col>
            <v-col>
              <v-text-field
                label="３パレット"
                type="number"
                v-model="line.pickup_times[2].seconds"
              />
            </v-col>
            <v-col>
              <v-text-field
                label="４パレット"
                type="number"
                v-model="line.pickup_times[3].seconds"
              />
            </v-col>
          </v-row>
        </v-container>
        <div>
          <v-text-field
            label="片付け時間 [sec/ピッチ]"
            type="number"
            v-model="line.finishing_time"
          />
        </div>
        <div>背景色</div>
        <v-color-picker
          class="ma-2"
          light
          v-model="color"
          :swatches="my_swatches"
          flat
          hide-canvas hide-mode-switch hide-inputs show-swatches>
        </v-color-picker>
        <div class="text-right">
          <v-btn @click="handleOK">決定</v-btn>
          <v-btn @click="handleCancel">キャンセル</v-btn>
        </div>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>

export default {
  props: {
    initialLine: {
      type: Object,
      required: false,
    },
    callback: {
      type: Function,
      required: true
    }
  },
  data: function() {
    return {
      my_swatches: [
        ['#FF0000', '#AA0000', '#550000'],
        ['#FFFF00', '#AAAA00', '#555500'],
        ['#00FF00', '#00AA00', '#005500'],
        ['#00FFFF', '#00AAAA', '#005555'],
        ['#0000FF', '#0000AA', '#000055'],
      ],
      line: this.initialLine
            ? this.initialLine
            : {
                id: null,
                name: "",
                ready_times: [
                  {pallet_num: 1, seconds: 25},
                  {pallet_num: 2, seconds: 45},
                  {pallet_num: 3, seconds: 65},
                  {pallet_num: 4, seconds: 87},
                ],
                pickup_times: [
                  {pallet_num: 1, seconds: 61},
                  {pallet_num: 2, seconds: 120},
                  {pallet_num: 3, seconds: 191},
                  {pallet_num: 4, seconds: 261},
                ],
                finishing_time: 50,
                color: {R: 50, G: 150, B: 140, A: 0}
              }
    }
  },
  computed: {
    color: {
      get() {
        function rgb2hex ( rgb ) {
          return "#" + ["R", "G", "B"].map(value => {
            return ( "0" + rgb[value].toString( 16 ) ).slice( -2 ) ;
          } ).join( "" ) ;
        }

        if (!this.line.color) return '#000000'
        else return rgb2hex( this.line.color )
      },
      set (hex) {
        function hex2rgb ( hex ) {
          if ( hex.slice(0, 1) == "#" ) hex = hex.slice(1) ;
          if ( hex.length == 3 ) hex = hex.slice(0,1) + hex.slice(0,1) + hex.slice(1,2) + hex.slice(1,2) + hex.slice(2,3) + hex.slice(2,3) ;

          const arr = [ hex.slice( 0, 2 ), hex.slice( 2, 4 ), hex.slice( 4, 6 ) ].map( function ( str ) {
            return parseInt( str, 16 )
          })
          return {
            'R': arr[0], 'G': arr[1], 'B': arr[2]
          }
        }
        this.line.color = hex2rgb(hex)
        console.log(this.line.color)
      }
    }
  },
  methods: {
    handleOK() {
      this.callback(this.line)
    },
    handleCancel() {
      this.callback({}, { canceled: true })
    }
  }
}
</script>
