<template>
  <v-dialog value="true" persistent>
    <v-card>
      <v-card-title>
        取り込み設定の編集
      </v-card-title>
      <v-card-text>
        <div>
          <v-text-field
            label="設定の名前"
            v-model="preset.name"
            hint="例: B行"
          />
        </div>
        <div>
          <v-text-field
              label="開始ピッチ"
              type="number"
              v-model="preset.start_pitch"
          />
        </div>
        <div>
          終了ピッチ
        </div>
        <div>
          <v-checkbox
            label="最後まで取り込む"
            v-model="import_to_the_end"
          ></v-checkbox>
        </div>
        <div>
          <v-text-field
              v-if="!import_to_the_end"
              label="終了ピッチ"
              type="number"
              v-model="finish_pitch"
          />
        </div>
        <v-text-field
            label="除外ピッチ"
            v-model="excluding_pitches"
            hint="例: 10,11,12"
        />
        <div class="text-right">
          <v-btn @click="handleOK">決定</v-btn>
          <v-btn @click="handleCancel">キャンセル</v-btn>
        </div>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>

export default {
  props: {
    initialPreset: {
      type: Object,
      required: false,
    },
    callback: {
      type: Function,
      required: true
    }
  },
  data: function() {
    return {
      preset: this.initialPreset
            ? this.initialPreset
            : {
                start_pitch: 1,
                excluding_pitches: []
              }
    }
  },
  computed: {
    finish_pitch: {
      get() {
        return this.preset.finish_pitch
      },
      set(val) {
        this.$set(this.preset, 'finish_pitch', val)
      }
    },
    import_to_the_end: {
      get() {
        return !this.preset.finish_pitch
      },
      set(val) {
        if (val) {
          this.$delete(this.preset, 'finish_pitch')
        } else {
          this.finish_pitch = 50
        }
      }
    },
    excluding_pitches: {
      get() {
        return this.preset.excluding_pitches.join(',')
      },
      set(val) {
        try {
          this.preset.excluding_pitches = val.split(',').map(v => Number(v))
        } catch(e) {
          console.error('cannot convert excluding_pitches to array', e)
        }
      }
    }
  },
  methods: {
    handleOK() {
      this.callback(this.preset)
    },
    handleCancel() {
      this.callback({}, { canceled: true })
    }
  }
}
</script>
